}

.Thanks-For {
  animation: spin 8s infinite linear;
  order: 1;
  width: 15vw;

  object-fit:cover;
  padding: 0px;
  margin: 0px;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}






